@import "variables";
.sidebar {
  height: 100vh;
  position: fixed;
  padding: 20px;
  left: 0;
  width: 70px;
  background-color: var(--main-bg);
  box-shadow: var(--box-shadow);
  z-index: 100;
  transition: width ease-in-out 0.3s;
  overflow: hidden;
  color: var(--txt-color);
  &__logo {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: flex-end;
    margin-bottom: 30px;
    i {
      margin-right: 5px;
      color: var(--main-color);
    }
    i,
    span {
      font-size: 25px;
    }
    span {
      font-weight: 700;
    }
    svg {
      width: 2rem;
    }
  }
  &__content {
    .sidebar__item {
      margin: 50px 0;

      &-title {
        margin: 20px 0;
      }
      &-iners {
        display: flex;
        flex-direction: column;
        a {
          color: var(--txt-color);
        }
      }
      &-iner {
        :hover {
          cursor: pointer;
          color: var(--main-color);
        }

        display: flex;
        padding: 10px 0;
        align-items: center;

        i {
          font-size: 30px;
          // margin-right: 20px;
        }
      }
      &-iner.active {
        color: var(--main-color);
      }
    }
  }
}
.txt {
  // display: none;
  font-size: 16px;
  filter: grayscale(100%) opacity(0);
  margin: 0 20px;
  transition: filter ease-in-out 0.3s;
}
.txt.logo {
  position: relative;
  left: -100%;
  transition: all ease-in-out 0.6s;
}
.sidebar:hover .txt {
  display: inline;
  filter: grayscale(0%) opacity(1);
}

.sidebar:hover {
  width: 220px;
}
.sidebar:hover .logo {
  left: 0;
}
.sidebar__logo svg {
  transition: all ease-in-out 0.3s;
}

.sidebar:hover .sidebar__logo svg {
  transform: rotate(-180deg);
}
@media screen and (max-width: 576px) {
  .sidebar {
    display: none;
  }
}
