@import "../scss/variables.scss";

table {
  width: 100%;
  border-spacing: 0;
}

thead {
  background-color: var(--second-bg);
}

tr {
  text-align: left;
}
th {
  background: var(--second-bg);
}
th,
td {
  text-transform: capitalize;
  // display: inline-block;
  padding: 10px 10px;
  font-size: 14px;
}

tbody > tr:hover {
  background-color: var(--main-color);
  color: var(--txt-white);
}
