@import "./variables.scss";
.dashboard {
  padding: 0 30px 30px 30px;
  color: var(--txt-color);
  &__content {
    // display: grid;
    // grid-template-columns: repeat(6, 1fr);
    // grid-template-rows: auto auto;
    // grid-gap: 30px;
    width: 100%;
    margin-top: 30px;
    &-status {
      // grid-column: 1/4;
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 10px;
      grid-template-rows: auto;
      .status__item {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        background: var(--main-bg);
        box-shadow: var(--box-shadow);
        padding: 15px 30px;
        border-radius: var(--border-radius);
        position: relative;
        z-index: 1;
        cursor: pointer;
        overflow: hidden;
        transition: scale 0.5s ease-in;

        &:hover::before {
          transform: scale(3);
        }
        &:hover {
          color: var(--txt-white);
        }
        &::before {
          content: "";
          width: 100%;
          position: absolute;
          z-index: -1;
          top: 0;
          left: -50%;
          padding-top: 100%;
          border-radius: 50%;
          background-image: linear-gradient(
            to top right,
            var(--main-color),
            var(--second-color)
          );
          transform: scale(0);
          transition: all 0.5s ease-in;
        }

        &-icon {
          font-size: 30px;
        }

        .count {
          font-size: 20px;
          font-weight: 700;
          margin: 5px 0;
        }
        .title {
          font-size: 16px;
        }
      }
    }
    &-chart {
      height: 200px;
      background: var(--main-bg);
      box-shadow: var(--box-shadow);
      border-radius: var(--border-radius);
    }
    &-customers {
      grid-column: 1/3;
      padding: 20px;
      background: var(--main-bg);
      border-radius: var(--border-radius);
      box-shadow: var(--box-shadow);
      overflow-x: auto;
    }
    &-orders {
      overflow-x: auto;
      padding: 20px;
      background: var(--main-bg);
      border-radius: var(--border-radius);
      box-shadow: var(--box-shadow);
    }
  }
}
