@import "./variables.scss";
.topnav {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: var(--topnav-height);
  color: var(--txt-color);
  &__input {
    display: flex;
    align-items: center;
    box-shadow: var(--box-shadow);
    overflow: hidden;
    border-radius: var(--border-radius);
    position: relative;
    input {
      padding: 10px 60px 10px 20px;
      font-size: 18px;
      outline: none;
      border: none;
      border-radius: var(--border-radius);
      background: var(--main-bg);
      color: var(--txt-color);
      &:focus {
        border: 1px solid var(--main-color);
      }
    }
    i {
      font-size: 1.5rem;
      position: absolute;
      right: 20px;
    }
  }
  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-img {
      width: 35px;
      height: 35px;
      object-fit: cover;
      border-radius: 50%;
      margin-left: 10px;
    }
    &-notification,
    &-changeColor {
      margin-left: 10px;
      cursor: pointer;
      i {
        font-size: 22px;
      }
    }
  }
}
